@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav-link {
    font-family: 'Roboto', sans-serif;
    font-weight: normal;
    color: #19345e !important;
}

.nav-link.active {
    color: #f47522 !important;
}
/* body {
    background: url('/src/assets/bg-img.png') no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
   
    -o-background-size: cover;
} */

img.background-image {
    background-position: center center fixed;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    width: 100%;
    height: 100%;
    background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
}
.user-photo img {
    width: 25%;
}

.user-photo {
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    top: 0;
    margin-top: 10rem;
}

.user-text {
    margin-left: 3rem;
}

h5 {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 25px;
    line-height: 29px;

    color: #19345e;
}

h1 {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 85px;
    line-height: 100px;
    /* identical to box height */

    color: #19345e;
}

h4 {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 35px;
    line-height: 41px;
    /* identical to box height */

    color: #19345e;
}

.user-text > .btn {
    width: 150px;
    height: 40px;
    left: 490px;
    top: 433px;
    color: white;
    background: #f47522;
    border-radius: 20px;
}

@media (max-width: 768px) {
    .user-photo {
        display: flex;
        flex-direction: column;
    }
    .user-text {
        margin-top: 2rem;
        margin-left: 4rem !important;
    }
    h5 {
        font-size: 15px;
        position: absolute;
    }
    h1 {
        font-size: 35px;
        height: 65px;
    }
    h4 {
        font-size: 25px;
    }
}

.left-image .background-first {
    width: 40%;
    height: 100%;
    position: fixed;

    display: inline-block;
}

.left-image .background-second {
    position: absolute;
    justify-content: center;
    align-items: center;
    left: 5%;
    width: 20%;
    top: 37%;
    display: block;
    float: left;
}
#myButton {
    margin-top: 2rem;
}
.content {
    display: flex;
    right: 5%;
}
.form-controller {
    font-size: 20px;
    color: #19345e;
    font-family: 'Roboto', sans-serif;
    position: relative;
    left: 10%;
    margin-top: 5rem;
}

.form-control {
    width: 35rem;
}
div#image-one {
    background-image: linear-gradient(
            to bottom,
            rgb(8, 18, 65) 1%,
            rgba(9, 44, 90, 0.6) 86%,
            rgba(9, 11, 133, 0.8) 120%
        ),
        url(/tugas-frontend-kampusmerdeka/static/media/charles-rRWiVQzLm7k-unsplash.0b577bce.jpg);

    color: white;
    width: 40vw;
    height: 100vh;
    background-size: 100% 100%;
    position: relative;
    right: 26%;
}

.btn.btn-contact {
    width: 100px;
    height: 40px;
    left: 490px;
    top: 433px;
    color: white !important;
    background: #f47522;
    border-radius: 20px;
}
a:hover {
    color: black !important;
}

span {
    font-family: 'Roboto', sans-serif;
    color: red;
    font-size: 25px;
}

@media (max-width: 768px) {
    .content {
        display: flex;
        flex-direction: column;
    }
    #image-one {
        width: 100vw;
        height: 80vw;
    }
    .left-image .background-second {
        width: 50vw;
        justify-content: center;
        align-items: center;
        left: 28%;
        top: 12rem;
    }

    .form-control {
        width: 30rem;
    }
}

@media (max-width: 375px) {
    body {
        overflow-x: hidden;
    }
    .left-image .background-second {
        width: 25vw;
        display: inline;
        left: 35%;
        top: 2rem;
    }
    .form-control {
        width: 15rem;
    }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type='number'] {
    -moz-appearance: textfield;
}

.line {
    width: 300px;
    border: 1px solid #ccd7eb;
}

.thanks > a.btn {
    width: 104px;
    height: 40px;
    left: 490px;
    top: 433px;
    color: white;
    background: #f47522;
    border-radius: 20px;
    margin-top: 1rem;
}

.card-container {
    top: 20%;
    position: relative !important;
}

.dot-images {
    top: 108%;
    right: 90%;
}

